import React, { Component } from 'react';

import './app-header.css';

export default class AppHeader extends Component {
    render() {
        const { toDo, doNe } = this.props;
        return (
            <div className="appheader">
                <h1>TODO LIST</h1>
                <div className="">
                    {toDo} more to do, {doNe} done
                </div>
            </div>
        );
    }
}
