import React, { Component } from 'react';
import AppHeader from '../app-header';
import SearchPanel from '../search-panel';
import ItemStatusFilter from '../item-status-filter';
import TodoList from '../todo-list';
import ItemAddForm from '../item-add-form';

import './app.css';

export default class App extends Component {
    numId = 100;
    state = {
        todoList: [
            this.creatNewItem('Drink Coffee'),
            this.creatNewItem('Buy App'),
            this.creatNewItem('Skill brain'),
        ],
        search: '',
        filter: 'all',
    };
    creatNewItem(label) {
        return {
            label,
            done: false,
            important: false,
            id: this.numId++,
        };
    }
    itemStateTemplate(arr, state, id) {
        const index = arr.findIndex((el) => el.id === id);
        const oldItem = arr[index];
        const newItem = { ...oldItem, [state]: !oldItem[state] };
        return [...arr.slice(0, index), newItem, ...arr.slice(index + 1)];
    }
    itemDone = (id) => {
        this.setState(({ todoList }) => {
            return {
                todoList: this.itemStateTemplate(todoList, 'done', id),
            };
        });
    };
    itemImportant = (id) => {
        this.setState(({ todoList }) => {
            return {
                todoList: this.itemStateTemplate(todoList, 'important', id),
            };
        });
    };
    itemDelete = (id) => {
        this.setState(({ todoList }) => {
            const index = todoList.findIndex((el) => el.id === id);
            const newArr = [
                ...todoList.slice(0, index),
                ...todoList.slice(index + 1),
            ];
            return {
                todoList: newArr,
            };
        });
    };
    itemAdd = (label) => {
        this.setState(({ todoList }) => {
            const newItem = this.creatNewItem(label);
            const newArr = [...todoList, newItem];
            return {
                todoList: newArr,
            };
        });
    };
    searchItem = (arr, searchText) => {
        if (searchText.length === 0) {
            return arr;
        }

        return arr.filter((item) => {
            return (
                item.label.toLowerCase().indexOf(searchText.toLowerCase()) > -1
            );
        });
    };
    searchInputText = (label) => {
        this.setState({
            search: label,
        });
    };
    filterState = (arr, filterState) => {
        switch (filterState) {
            case 'all':
                return arr;
            case 'active':
                return arr.filter((item) => !item.done);
            case 'done':
                return arr.filter((item) => item.done);
            default:
                return arr;
        }
    };
    switchFilterState = (label) => {
        this.setState({
            filter: label,
        });
    };
    render() {
        const { todoList, search, filter } = this.state;
        const doNe = todoList.filter((el) => el.done).length;
        const toDo = todoList.length - doNe;
        const visibleItems = this.filterState(
            this.searchItem(todoList, search),
            filter
        );
        return (
            <div className="wrap">
                <div className="main">
                    <AppHeader toDo={toDo} doNe={doNe} />
                    <div className="searchblock">
                        <SearchPanel searchInputText={this.searchInputText} />
                        <ItemStatusFilter
                            switchFilterState={this.switchFilterState}
                            filter={filter}
                        />
                    </div>
                    <TodoList
                        todoList={visibleItems}
                        itemDone={this.itemDone}
                        itemImportant={this.itemImportant}
                        itemDelete={this.itemDelete}
                    />
                    <div>
                        <ItemAddForm itemAdd={this.itemAdd} />
                    </div>
                </div>
            </div>
        );
    }
}
