import React from 'react';
import TodoListItem from '../todo-list-item';

import './todo-list.css';

const TodoList = ({ todoList, itemDone, itemImportant, itemDelete }) => {
    const todoListItem = todoList.map((item) => {
        const { id, ...otherInfo } = item;
        return (
            <li className="list-group-item" key={id}>
                <TodoListItem
                    otherInfo={otherInfo}
                    itemDone={() => itemDone(id)}
                    itemImportant={() => itemImportant(id)}
                    itemDelete={() => itemDelete(id)}
                />
            </li>
        );
    });

    return <ul className="list-group todo-list">{todoListItem}</ul>;
};

export default TodoList;
