import React, { Component } from 'react';

export default class ItemStatusFilter extends Component {
    buttonsArr = [
        { nameButt: 'all', label: 'All' },
        { nameButt: 'active', label: 'Active' },
        { nameButt: 'done', label: 'Done' },
    ];
    render() {
        const buttonList = this.buttonsArr.map(({ nameButt, label }) => {
            const { switchFilterState, filter } = this.props;
            const isActive = filter === nameButt;
            const clazz = isActive ? 'btn-info' : 'btn-outline-secondary';
            return (
                <button
                    type="button"
                    className={`btn ${clazz}`}
                    key={nameButt}
                    onClick={() => switchFilterState(nameButt)}
                >
                    {label}
                </button>
            );
        });
        return <div className="btn-group">{buttonList}</div>;
    }
}
